@font-face {
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff") format("woff"), url("../fonts/oswaldmedium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldregular.woff") format("woff"), url("../fonts/oswaldregular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

input,
button,
textarea {
  font-family: "Oswald", "Arial", sans-serif;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

input[type=radio],
input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.button {
  display: block;
  background-color: #68b738;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  width: 100%;
  text-align: center;
  padding: 12px 0;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--light {
  background-color: #f2f2f2;
  color: #444444;
  font-size: 16px;
}
.button--light:hover {
  background-color: #ebebeb;
}
.button--light:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 1;
    padding: 16px 0;
  }
}

.input,
.textarea {
  width: 100%;
  padding: 13px 15px 12px;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
  box-sizing: border-box;
}
.input:hover,
.textarea:hover {
  border-color: #cdcdcd;
}
.input:focus,
.textarea:focus {
  border-color: #212735;
  box-shadow: inset 0 0 0 1px #212735;
  outline: 0;
}
.input::placeholder,
.textarea::placeholder {
  color: #444444;
  opacity: 1;
}

.input {
  height: 45px;
}
@media (min-width: 768px) {
  .input {
    padding: 11px 20px;
    font-size: 20px;
    line-height: 30px;
    height: 50px;
  }
}

.textarea {
  height: 150px;
  padding-right: 10px;
  resize: none;
}
@media (min-width: 768px) {
  .textarea {
    height: 170px;
    font-size: 20px;
    line-height: 30px;
    padding-top: 4px;
    padding-left: 19px;
  }
}

.label {
  text-transform: uppercase;
  font-size: 16px;
  line-height: 18px;
  margin-right: 13px;
  color: #444444;
}
.label--radio, .label--checkbox {
  position: relative;
  display: block;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 46px;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
  user-select: none;
}
.label--radio::before, .label--checkbox::before {
  content: "";
  position: absolute;
  display: block;
  top: 6px;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
.label--radio:hover::before, .label--checkbox:hover::before {
  border-color: #bababa;
}
.label--radio:active, .label--checkbox:active {
  opacity: 0.3;
}
.label--radio::before {
  padding: 8px;
  border-radius: 50%;
}
.label--checkbox::before {
  padding: 6px 3px 6px 4px;
}
@media (min-width: 768px) {
  .label {
    font-size: 20px;
    line-height: 30px;
    margin-right: 18px;
  }
  .label--radio::before, .label--checkbox::before {
    width: 40px;
    height: 40px;
  }
  .label--radio, .label--checkbox {
    font-size: 20px;
    line-height: 29px;
    padding-left: 59px;
  }
}

.container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 15px;
  }
}

.radio:checked ~ .label--radio::before {
  background-color: #68b738;
  background-clip: content-box;
}

.checkbox:checked ~ .label--checkbox::before {
  background: url("../img/icons/tick.svg") no-repeat center;
  background-size: 21px auto;
  background-clip: content-box;
}

.separate-title {
  position: relative;
  display: inline-block;
  font-size: 24px;
  padding-bottom: 36px;
  padding-right: 22px;
  background-color: #ffffff;
}
.separate-title::after, .separate-title::before {
  content: "";
  height: 2px;
  background-color: #68b738;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
}
.separate-title::before {
  display: none;
  margin-top: calc(0.5em + 3px);
}
.separate-title::after {
  margin-top: calc(0.5em + 3px);
}
@media (min-width: 768px) {
  .separate-title {
    padding-bottom: 0;
    margin-bottom: 82px;
    font-size: 36px;
    border-bottom: none;
    padding-left: 37px;
    padding-right: 37px;
  }
  .separate-title::after, .separate-title::before {
    margin-top: calc(0.5em + 6px);
  }
  .separate-title::before {
    display: block;
    right: 100%;
  }
}
@media (min-width: 1440px) {
  .separate-title {
    padding-left: 0;
    margin-bottom: 80px;
  }
  .separate-title::before {
    margin-right: 37px;
  }
}

.section-title {
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 60px;
    line-height: 1;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1;
  height: 100%;
  min-width: 320px;
}

@media (min-width: 768px) {
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header__wrapper {
  position: relative;
  z-index: 3;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 65px;
}
@media (min-width: 768px) {
  .header__wrapper {
    height: 100px;
    flex-grow: 1;
    flex-shrink: 0;
    justify-content: flex-start;
    margin-right: 20px;
    background-color: transparent;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    height: 163px;
  }
}

.header__logo:hover {
  opacity: 0.8;
}
.header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .header__logo--additional {
    display: none;
  }
}

.header__nav {
  position: absolute;
  top: -100%;
  left: 0;
  right: 0;
  z-index: 2;
  transition: top 0.4s ease 0s;
}
.header__nav--active {
  top: 65px;
}
.header__nav--catalog .header__nav-link, .header__nav--form .header__nav-link {
  color: #000000;
}
.header__nav--catalog .header__nav-item--active::before, .header__nav--form .header__nav-item--active::before {
  background-color: #68b738;
}
@media (min-width: 768px) {
  .header__nav {
    padding-top: 7px;
    position: static;
    display: flex;
    justify-content: flex-end;
  }
}

.header__nav-list {
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header__nav-list {
    display: flex;
    flex-wrap: wrap;
    background-color: transparent;
  }
}

.header__nav-item {
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  text-align: center;
  padding: 22px 10px;
  border-top: 1px solid #e6e6e6;
  color: #000000;
}
.header__nav-item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .header__nav-item:last-child {
    border: none;
  }
}
@media (min-width: 768px) {
  .header__nav-item {
    padding: 10px 0;
    border: none;
  }
  .header__nav-item:not(:last-child) {
    margin-right: 36px;
  }
}
@media (min-width: 1440px) {
  .header__nav-item {
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
}

.header__nav-link {
  color: inherit;
}
.header__nav-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.header__nav-link:active {
  color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) {
  .header__nav-item--active {
    position: relative;
  }
  .header__nav-item--active::before {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .header__nav-item--active::before {
    background-color: #ffffff;
  }
}

.header__toggle {
  position: relative;
  width: 26px;
  height: 16px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__toggle-line {
  position: absolute;
  left: 0;
  top: calc(50% - 1px);
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 0.4s ease 0s;
}
.header__toggle-line:first-child {
  top: 0;
}
.header__toggle-line:last-child {
  top: auto;
  bottom: 0;
}

.header__toggle--active .header__toggle-line {
  transform: scale(0);
}
.header__toggle--active .header__toggle-line:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}
.header__toggle--active .header__toggle-line:last-child {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
}

.header--nojs .header__toggle {
  display: none;
}
.header--nojs .header__nav {
  position: static;
}

.footer {
