}

.footer__icon {
  transition: all 0.2s ease 0s;
  fill: #666666;
}
.footer__icon:hover {
  fill: #68b738;
}
.footer__icon:active {
  fill: #68b738;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .footer__icon--vk {
    width: 30px;
    height: 22px;
  }
}
@media (min-width: 768px) {
  .footer__icon--instagram {
    width: 23px;
    height: 23px;
  }
}
@media (min-width: 768px) {
  .footer__icon--facebook {
    width: 28px;
    height: 30px;
  }
}

