.services__goto {
  color: #000000;
  display: flex;
  align-items: center;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 400;
  background-color: inherit;
  cursor: pointer;
  transition: opacity 0.3s ease 0s;
}
.services__goto:active {
  opacity: 0.3;
}
.services__goto::after {
  content: "";
  background-image: url("../img/icons/arrow.svg");
  width: 24px;
  height: 12px;
  margin-left: 15px;
}
@media (min-width: 768px) {
  .services__goto {
    font-size: 20px;
    line-height: 30px;
