.cat-form__common {
  margin-bottom: 26px;

  @media (min-width: $tablet-width) {
    margin-bottom: 71px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    margin-bottom: 65px;

    fieldset {
      flex: 1 1 50%;

      &:not(:last-child) {
        margin-right: 80px;
      }
    }
  }
}

//** cat-form__common-set **//
.cat-form__common-set {
  padding-bottom: 19px;

  @media (min-width: $tablet-width) {
    margin-bottom: 72px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.cat-form__common-set-item {
  display: flex;
  align-items: center;
  padding: 0 15px;

  &:not(:last-child) {
    margin-bottom: 14px;
  }

  @media (min-width: $tablet-width) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.cat-form__common-set-input {
  flex-grow: 1;
}

//** cat-form__action-set **//
.cat-form__action-set {
  padding-top: 22px;
  padding-bottom: 19px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    border: 1px solid #d9d9d9;
    padding: 27px;
  }
}

.cat-form__action-set-item {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
  padding: 0 15px;

  &:not(:last-child) {
    margin-bottom: 13px;
  }
}

.cat-form__action-set-radio {
  height: 30px;
  margin-right: 15px;
}

.cat-form__action-set-label {
  flex-grow: 1;
}

//** cat-form__contacts-set **//
.cat-form__contacts-set {
  padding: 26px 15px;

  @media (min-width: $desktop-width) {
    display: flex;
  }
}

//** cat-form__additional-set **//
.cat-form__additional-set {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}

.cat-form__legend {
  font-size: 24px;
  line-height: 26px;
}

.cat-form__small {
  font-size: 14px;
  line-height: 16px;
}
